

.navbar-wrapper{
    padding: 0px 3%;
    height: 137px;
    display: flex;
    margin-top: 30px;
    
}

.navbar-wrapper img{
    max-width: 196px;
    max-height: 103px;
    margin-top: 20px;
}

@media (max-width: 810px) {
    .navbar-wrapper{
        margin-top: unset;
    }
}



@media (max-width: 705px) {
    .navbar-wrapper{
        justify-content: center;
    }
}

